.empty {
      margin: 2rem auto;
      text-align: center;
      font-size: 1.5rem;
      font-weight: bold;
    }
.next {
  outline: none;
  transform: translateX(-200%);
  width: 3rem;
  font-size: 1.2rem;
  color: #d5293f !important;
  font-weight: bold;
  background-color: rgba(250, 249, 247, 0.4) !important;
  height: 3rem;
  border-radius: 50%;
}

.prev {
  outline: none;
  transform: translateX(200%);
  width: 3rem;
  font-size: 1.2rem;
  color: #d5293f !important;
  font-weight: bold;
  background-color: rgba(250, 249, 247, 0.4) !important;
  height: 3rem;
  border-radius: 50%;
}

.pagingDots {
  fill: #6b6b6b !important;
  outline: none;
  border-radius: 50%;
  margin: 0.5rem;
  background-color: var(--secondary-color) !important;
  transform: scale(0.8);
}
.header {
  display: block;
  font-size: 30px;
  font-weight: bold;
  width: 80%;
  margin: auto;
}
.Carousel {
  width: 80%;
  margin: 2rem auto;
}
.carouselImg {
  height: 30rem;
  object-fit: cover;
  display: flex;
  justify-content: center;
  align-items: center;
  img {
    width: 100%;
    object-fit: contain;
  }
}

.Upload {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 2rem 0 0 0;
  img {
    width: 10rem;
    height: 10rem;
    margin: 1rem;
    object-fit: cover;
    border-radius: 50%;
  }
  .imgs {
    display: flex;
    flex-wrap: wrap;
    margin: 1rem 0;
    justify-content: center;
  }
  .imgConatiner {
    position: relative;
    span {
      position: absolute;
      top: 12px;
      right: 20px;
      svg {
        width: 0.7rem;
      }
      &:hover {
        svg {
          path {
            fill: #d5293f;
          }
        }
      }
    }
  }
  .input {
    width: 120px;
    &::-webkit-file-upload-button {
      visibility: hidden;
    }
    &::before {
      content: "Select Images";
      display: inline-block;
      background: #fff;
      border: 1px solid #999;
      border-radius: 3px;
      padding: 5px 10px;
      outline: none;
      white-space: nowrap;
      // -webkit-user-select: none;
      cursor: pointer;
      text-shadow: 1px 1px #fff;
      font-weight: 700;
      font-size: 10pt;
    }
    &::after {
      display: none;
    }
    &:hover::before {
      border-color: black;
    }
    &:active::before {
      background: -webkit-linear-gradient(top, #e3e3e3, #f9f9f9);
    }
  }
  .submit {
    display: inline-block;
    min-width: 150px;
    height: 30px;
    // margin: 20px auto 0 auto;
    background: #8bc34a;
    color: #fefefe;
    font-size: 1em;
    border-radius: 4px;
    text-align: center;
    position: relative;
    cursor: pointer;
    appearance: none;
    -webkit-appearance: none;
    border: 0;
  }
}
